.itemListTopContainer{
    width: 400px;
}

.listItemInput{
    width: 270px;
}

@media screen and (max-width: 600px){
    .itemListTopContainer{
        width: 350px;
    }
    .listItemInput{
        width: 236px;
    }
}
.listTitle{
    height: 50px;
    width: 400px;
    margin: 50px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: x-large;
}
@media screen and (max-width: 600px){
    .listTitle{
        width: 350px;
    }
}
.listEdit{
    height: 200px;
    width: 250px;
    margin-top: 125px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media screen and (max-width: 600px){
    .listEdit{
        margin-top: 75px;
        margin-left: 0px;
    }
}
.createListTopContainer {
  width: 400px;
}
.newListInput {
  width: 270px;
}
@media screen and (max-width: 600px) {
  .createListTopContainer {
    width: 300px;
  }
  .newListInput {
    width: 236px;
  }
}

.topContainer{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.title{
    height: 50px;
    width: 350px;
    margin: 50px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: x-large;
}
.roltopContainer{
    width: 300px;
}
.username,.password{
    width: 300px;
}
.lorButton{
    width: 140px;
}
.lorButtons{
    width: 300px;
    display: flex;
    justify-content: space-between;
}

